      option: {
        backgroundColor: '#2c343c',
        textStyle: {
          color: 'rgba(255, 255, 255, 255)'
        },
        title: {
          text: '',
          textStyle: {
            color: 'rgba(255, 255, 255, 255)'
          }
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['', '', ''],
          textStyle: {
            color: 'rgba(255, 255, 255, 255)'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
