<template>
  <div class="blogs">
    <div class="content-header">
      <h1><small></small></h1>
      <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
        <el-breadcrumb-item ></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!---->
    <div class="app-container">
      <div class="box">
        <el-form :model="formData" :rules="userRules" ref="userForm" label-width="100px">
          <el-row>
            <el-col :span="8">
              <el-form-item prop="username" label="" >
                <el-input v-model="formData.username" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="password" label="">
                <el-input v-model="formData.password"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
         

            <el-form-item prop="avatar" label="">
              <div style="height: 100px">
              <el-input style="width: 78%" v-model="formData.avatar"></el-input>
            
            </div>
            </el-form-item>

            <el-form-item prop="avatar" label=""> <el-upload
                style="margin-top: 10px;"
                class="avatar-uploader"
                action="serverApi/oss/userAvatar/"
                accept="image/png,.jpg"
                multiple
                :limit="1"
                :on-exceed="masterFileMax"
                :show-file-list="false"
                :http-request="uploadPic"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload></el-form-item>


          <el-form-item>
            <el-button type="primary" @click="submitForm" style="margin-top: 50px"></el-button>
<!--            <el-button @click="resetForm('formData')"></el-button>-->
          </el-form-item>




        </el-form>
      </div>
    </div>
    <br>
    <br>
  </div>
</template>

<script>
export default {
  data () {
    return {

      imageUrl: '',

    // 
      formData: {},



      userRules: {
        // 
        
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: ' 6  20 ', trigger: 'blur' }
        ],
      
        avatar: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 150, message: ' 1  150 ', trigger: 'blur' }
        ]
      }
    }
  },


  created () {


    this.getUser()


  },




  methods: {


    masterFileMax (files, fileList) {
      console.log(files, fileList)
      this.$message.warning('')
    },




     uploadPic (param) {
      var fileObj = param.file
      var form = new FormData()
      // 
      form.append('file', fileObj)
      const { data: res } =  this.$http.post('/serverApi/oss/userAvatar', form)
      if (res.flag) {
        // 
        this.$message.success('')
        this.formData.avatar = res.data.url
        console.log(res.data.url)
      } else { // 
        this.$message.error(res.message)
      }
    },



    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },



    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt5M = file.size / 1024 / 1024 < 5

      if (!isJPG) {
        this.$message.error(' JPG !')
      }
      if (!isLt5M) {
        this.$message.error('5MB!')
      }
      return isJPG && isLt5M
    },


    
    submitForm () {
      // 
      this.$refs.userForm.validate((valid) => {
        if (valid) 
        
        {
          // ajax
          
          this.$axios.put('/user', this.formData).then((res) => {
            // 

            //sessionStorage user      JSON.stringify  sessionStorage

           
            sessionStorage.setItem('user',JSON.stringify(this.formData))

            if (res.data.flag) {
              // 
              this.$message({
                message: '',
                type: 'success'
              })
            } else { // 
              this.$message.error(res.data.message)
            }
          })
        } 
        
        
        
        else 
        { // 
          this.$message.error('')
          return false
        }


      })
    },


    resetForm (formName) {
      this.$refs[formName].resetFields()
    },




      // 
    getUser () {


      this.user = window.sessionStorage.getItem('user')
      if (this.user != null) {


     
        this.formData = JSON.parse(this.user)
        
       
      

      }
    }
  }





}
</script>

<style lang="less" scoped>
  @import "../../assets/css/me.css";
  @import "../../assets/css/style.css";
</style>
